window.addEventListener('DOMContentLoaded', () => {
    fix100vh();
    window.addEventListener('resize', () => {
        fix100vh();
        getScrollBarSize()
    })

    getScrollBarSize()

})

function fix100vh() {
    let vh = window.innerHeight * 0.01;
    document.documentElement.style.setProperty('--vh', `${vh}px`);
}

$(".qa-card__show").click(function () {
    $(this).parents(".qa-card").toggleClass("_active")
    $(this).siblings(".qa-card__hidden").slideToggle()
})


$(".program-item__btn").click(function () {
    $(this).parents(".program-item").toggleClass("_active")
    $(this).parents(".program-item").find(".program-detail").slideToggle()
})

$("._show-all-lessons").click(function () {
    $(".program-item._type1:not(._show-always)").toggleClass("_hide")
    $(this).toggleClass("_active")
})

$("._show-all-lessons2").click(function () {
    $(".program-item._type2:not(._show-always)").toggleClass("_hide")
    $(this).toggleClass("_active")
})
